import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import MobileStepper from '@material-ui/core/MobileStepper'
import Button from '@material-ui/core/Button'
import KeyboardArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import SwipeableViews from 'react-swipeable-views'

const styles = theme => ({
  root: {
    maxWidth: 400,
    flexGrow: 1
  },
  img: {
    height: 255,
    maxWidth: 400,
    overflow: 'hidden',
    width: '100%'
  }
})

class ProductImageArea extends Component {
  constructor () {
    super()
    this.state = {
      activeStep: 0
    }
  }

  handleNext = () => {
    this.setState(prevState => ({
      activeStep: prevState.activeStep + 1
    }))
  }

  handleBack = () => {
    this.setState(prevState => ({
      activeStep: prevState.activeStep - 1
    }))
  }

  handleStepChange = activeStep => {
    this.setState({ activeStep })
  }

  render () {
    const {activeStep} = this.state
    const {classes, product, theme} = this.props
    const {uploadItemList} = product
    const maxSteps = (uploadItemList ? uploadItemList.length : 0)

    return (
      <div className={classes.root}>
        <SwipeableViews
          axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
          index={this.state.activeStep}
          onChangeIndex={this.handleStepChange}
          enableMouseEvents>
          {uploadItemList.map(item => (
            <img key={item.timestamp} className={classes.img} src={item.onlinePath} alt='product' />
          ))}
        </SwipeableViews>
        <MobileStepper
          steps={maxSteps}
          position='static'
          activeStep={activeStep}
          className={classes.mobileStepper}
          nextButton={
            <Button size='small' onClick={this.handleNext} disabled={activeStep === maxSteps - 1}>
              Next
              {theme.direction === 'rtl' ? <KeyboardArrowLeftIcon /> : <KeyboardArrowRightIcon />}
            </Button>
          }
          backButton={
            <Button size='small' onClick={this.handleBack} disabled={activeStep === 0}>
              {theme.direction === 'rtl' ? <KeyboardArrowRightIcon /> : <KeyboardArrowLeftIcon />}
              Back
            </Button>
          } />
      </div>
    )
  }
}

export default withStyles(styles, {withTheme: true})(ProductImageArea)



