import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../../../constant'

import Grid from '@material-ui/core/Grid'
import ButtonBase from '@material-ui/core/ButtonBase'
import Fade from '@material-ui/core/Fade'
import Paper from '@material-ui/core/Paper'
import CircularProgress from '@material-ui/core/CircularProgress'
import Typography from '@material-ui/core/Typography'

const styles = () => ({
  root: {
    margin: '.5rem .5rem',
    cursor: 'pointer'
  },
  cardArea: {
    padding: '.5rem'
  },
  imgArea: {
    maxHeight: '5rem',
    marginBottom: '1rem'
  },
  imgLoader: {
    margin: '2rem'
  },
  productImg: {
    width: '100%',
    height: '5rem',
    boxShadow: '0 0 .5rem rgba(0, 0, 0, .3)'
  },
  productContent: {
    paddingLeft: '.5rem'
  },
  hidden: {
    display: 'none'
  },
  price: {
    marginTop: '1rem'
  }
})

class ProductCard extends Component {
  constructor () {
    super()
    this.state = {
      imgIndex: 0,
      isImgLoaded: false,
      preloadImgList: []
    }
  }

  componentWillUnmount () {
    this.isCancelled = true
  }

  componentDidMount () {
    if (this.isCancelled) return

    const {product} = this.props
    const {uploadItemList} = product

    const preloadImgRequestList = uploadItemList.map(item => {
      return new Promise((resolve, reject) => {
        const preloadImg = new Image()
        preloadImg.src = item.onlineCoverPath
        preloadImg.onload = () => {
          setTimeout(() => resolve(preloadImg), 1000)
        }
        preloadImg.onerror = (error) => reject(error)
      })
    })

    // safe img to memory protect from pre get again
    Promise.all(preloadImgRequestList)
      .then(preloadImgList => {
        this.setState({
          preloadImgList,
          isImgLoaded: true
        })
      })
  }

  handleAnimation = () => {
    if (this.isCancelled) return

    const {product} = this.props
    const {uploadItemList} = product
    const isSingleImg = (uploadItemList.length === 1)

    if (!isSingleImg) {
      setTimeout(() => {
        this.updateImgIndex(uploadItemList)
        setTimeout(() => this.updateImgIndex(uploadItemList), 2000)
      }, 1000)
    }
  }

  updateImgIndex = (uploadItemList) => {
    if (this.isCancelled) return

    if (this.state.imgIndex === (uploadItemList.length - 1)) {
      this.setState({imgIndex: 0})
    } else {
      this.setState({imgIndex: this.state.imgIndex + 1})
    }
  }

  render () {
    const {imgIndex, isImgLoaded} = this.state
    const {classes, product, onOpenProductModal} = this.props
    const {title, uploadItemList, discountPrice} = product
    const isSingleImg = (uploadItemList.length === 1)

    return (
      <Fade in>
        <Paper
          className={classes.root}
          onClick={() => onOpenProductModal(product, CONSTANT.CREATE)}>
          <ButtonBase>
            <Grid container className={classes.cardArea}>
              <Grid item xs={12} container justify='center' alignItems='center'>
                <Grid item container justify='center' className={classes.imgArea}>
                  {
                    isImgLoaded
                      ? <img
                        className={`${classes.productImg} ${isSingleImg ? '' : 'rotating'} `}
                        src={uploadItemList[imgIndex].onlineCoverPath}
                        alt='product'
                        onAnimationStart={this.handleAnimation}
                        onAnimationIteration={this.handleAnimation} />
                      : <CircularProgress size={30} className={classes.imgLoader} />
                  }
                </Grid>
              </Grid>
              <Grid item xs={12} className={classes.productContent}>
                {title}
              </Grid>
              <Grid item xs={12} container>
                <Grid item className={classes.price}>
                  <Typography gutterBottom variant='caption' align='left'>Price</Typography>
                  <Typography gutterBottom variant='title' color='error'>
                    $&nbsp;{discountPrice}
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </ButtonBase>
        </Paper>
      </Fade>
    )
  }
}

export default withStyles(styles)(ProductCard)



