import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../../../../constant'

import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Checkbox from '@material-ui/core/Checkbox'

const styles = theme => ({
  root: {},
  price: {
    marginRight: '1rem'
  },
  originPrice: {
    color: 'rgba(0,0,0,.5)',
    textDecoration: 'line-through'
  }
})

class ProductInfoArea extends Component {
  constructor () {
    super()
    this.state = {
      checked: [0]
    }
  }

  handleToggle = value => () => {
    console.log(value)
    const { checked } = this.state
    const currentIndex = checked.indexOf(value)
    const newChecked = [...checked]

    if (currentIndex === -1) {
      newChecked.push(value)
    } else {
      newChecked.splice(currentIndex, 1)
    }

    this.setState({
      checked: newChecked
    })
  };

  render () {
    const {classes, modalType, product, selectedSpecList, updateSelectSpecList} = this.props
    const {discription, originPrice, discountPrice, selectSpecList, cart} = product
    const newSpecList = (cart ? cart.selectedSpecList : selectSpecList)

    return (
      <Grid container justify='center'>
        <Grid item xs={12}>
          <Typography variant='caption' gutterBottom>Discription</Typography>
          <Typography variant='body2' gutterBottom>{discription}</Typography>
        </Grid>
        <Grid item xs={12}>
          <Typography variant='caption' gutterBottom>Please select a spec</Typography>
          <List>
            {newSpecList.map((spec, index) => (
              <ListItem
                key={index}
                role={undefined}
                dense
                button
                onClick={() => updateSelectSpecList(spec)}
                divider={index !== (selectSpecList.length - 1)}>
                {
                  modalType === CONSTANT.CREATE &&
                  <Checkbox
                    color='primary'
                    checked={selectedSpecList.indexOf(spec) !== -1}
                    tabIndex={-1}
                    disableRipple />
                }
                <ListItemText>
                  <Typography gutterBottom variant='caption'>Size</Typography>
                  <Typography gutterBottom variant='body2'>{spec.size}</Typography>
                </ListItemText>
                <ListItemText>
                  <Typography gutterBottom variant='caption'>Color</Typography>
                  <Typography gutterBottom variant='body2'>{spec.color}</Typography>
                </ListItemText>
                <ListItemText>
                  <Typography gutterBottom variant='caption'>Inventory</Typography>
                  <Typography gutterBottom variant='body2'>{spec.inventory}</Typography>
                </ListItemText>
              </ListItem>
            ))}
          </List>
        </Grid>
        <Grid item xs={12} container>
          <Grid item className={classes.price}>
            <Typography gutterBottom variant='caption'>Price</Typography>
            <Typography
              gutterBottom
              variant='subheading'
              className={classes.originPrice}>$&nbsp;{originPrice}</Typography>
          </Grid>
          <Grid item>
            <Typography gutterBottom variant='display2' color='error'>$&nbsp;{discountPrice}</Typography>
          </Grid>
        </Grid>
      </Grid>
    )
  }
}

export default withStyles(styles)(ProductInfoArea)



