import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import * as CONSTANT from '../../../constant'

import Grid from '@material-ui/core/Grid'
import Dialog from '@material-ui/core/Dialog'
import withMobileDialog from '@material-ui/core/withMobileDialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import Button from '@material-ui/core/Button'

import ProductImageArea from './ProductImageArea/ProductImageArea'
import ProductInfoArea from './ProductInfoArea/ProductInfoArea'

const styles = theme => ({
  root: {
    width: '100%',
    maxWidth: '80vw',
    [theme.breakpoints.only('xs')]: {
      maxWidth: '100vw'
    }
  },
  productImageArea: {
    paddingRight: '.5rem'
  },
  productInfoArea: {
    paddingLeft: '.5rem',
    [theme.breakpoints.only('xs')]: {
      paddingTop: '.5rem'
    }
  }
})

const ProductModal = (props) => {
  const {
    classes,
    selectProduct,
    isModalOpen,
    toggleModal,
    fullScreen,
    modalType,
    onAddToCart,
    selectedSpecList,
    updateSelectSpecList,
    onDeleteFromCart
  } = props

  return (
    <Dialog
      open={isModalOpen}
      onClose={toggleModal}
      fullScreen={fullScreen}
      aria-labelledby='form-dialog-title'
      classes={{
        paper: classes.root
      }}>
      <DialogTitle id='form-dialog-title'>
        {selectProduct.title}
      </DialogTitle>
      <DialogContent>
        <Grid container>
          <Grid item xs={12} sm={6} className={classes.productImageArea}>
            <ProductImageArea
              product={selectProduct} />
          </Grid>
          <Grid item xs={12} sm={6} className={classes.productInfoArea}>
            <ProductInfoArea
              modalType={modalType}
              product={selectProduct}
              selectedSpecList={selectedSpecList}
              updateSelectSpecList={CONSTANT.CREATE ? updateSelectSpecList : null} />
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        {
          modalType === CONSTANT.CREATE &&
          <Button
            color='primary'
            onClick={() => onAddToCart(selectProduct)}
            disabled={selectedSpecList.length === 0}>
            ADD TO CART
          </Button>
        }
        {
          modalType === CONSTANT.DELETE &&
          <Button color='primary' onClick={() => onDeleteFromCart(selectProduct)}>
            DELETE FROM CART
          </Button>
        }
        <Button color='primary' onClick={toggleModal}>
          CANCEL
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default withStyles(styles)(withMobileDialog()(ProductModal))



