import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import ProductListContent from '../component/ProductList/ProductListContent'

class ProductList extends Component {
  componentDidMount () {
    const {setDataLoaded, getProductList, getShoppingCartList} = this.props

    Promise.all([
      setDataLoaded(false),
      getProductList()
    ])
      .then(() => {
        getShoppingCartList()
      })
  }

  render () {
    return (
      <main>
        <ProductListContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    isDataLoaded: state.isDataLoaded,
    productList: state.productList,
    selectProduct: state.selectProduct,
    isModalOpen: state.isModalOpen,
    selectedSpecList: state.selectedSpecList,
    modalType: state.modalType
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getProductList: () => dispatch(actionCreators.getProductList()),
    getShoppingCartList: () => dispatch(actionCreators.getShoppingCartList()),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    toggleModal: () => dispatch(actionCreators.toggleModal()),
    updateModalType: (modalType) => dispatch(actionCreators.updateModalType(modalType)),
    setSelectProduct: (selectProduct) => dispatch(actionCreators.setSelectProduct(selectProduct)),
    addShoppingCartItem: (selectProduct) => dispatch(actionCreators.addShoppingCartItem(selectProduct)),
    addLocalShoppingCartItem: (selectProduct) => dispatch(actionCreators.addLocalShoppingCartItem(selectProduct)),
    updateSelectSpecList: (selectSpec) => dispatch(actionCreators.updateSelectSpecList(selectSpec))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductList)



