import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'

import ProductCard from './ProductCard/ProductCard'
import ProductModal from '../Common/ProductModal/ProductModal'

const styles = () => ({
  root: {
  },
  loader: {
    margin: '5rem'
  }
})

class ProductListContent extends Component {
  constructor () {
    super()
    this.state = {}
  }

  onOpenProductModal = (product, modalType) => {
    const {
      updateModalType,
      setSelectProduct,
      toggleModal
    } = this.props

    Promise
      .all([
        updateModalType(modalType),
        setSelectProduct(product)
      ]).then(() => {
        toggleModal()
      })
  }

  onAddToCart = (selectProduct) => {
    const {
      addShoppingCartItem,
      addLocalShoppingCartItem,
      selectedSpecList,
      toggleModal
    } = this.props
    const timestamp = Date.now()
    const newSelectProduct = {
      ...selectProduct,
      selectedSpecList,
      timestamp,
      cart: {
        selectedSpecList,
        key: timestamp,
        timestamp
      },
      author: 'foo',
      status: 'wait',
      address: ' ****'
    }

    addShoppingCartItem(newSelectProduct)
    addLocalShoppingCartItem({...newSelectProduct, isLoading: true})
    toggleModal()
  }

  render () {
    const {
      classes,
      productList,
      isDataLoaded,
      isModalOpen,
      modalType,
      selectProduct,
      toggleModal,
      selectedSpecList,
      updateSelectSpecList
    } = this.props

    return (
      <div className={classes.root}>
        <Grid container direction='row'>
          {
            productList
              .map(product => (
                <Grid item key={product.key} xs={6} sm={4}>
                  <ProductCard
                    product={product}
                    onOpenProductModal={this.onOpenProductModal} />
                </Grid>
              ))
          }
          {
            !isDataLoaded &&
            <Grid item xs={12} container justify='center'>
              <CircularProgress size={80} className={classes.loader} />
            </Grid>
          }
        </Grid>
        {
          Object.keys(selectProduct).length > 0 &&
          <ProductModal
            selectProduct={selectProduct}
            isModalOpen={isModalOpen}
            modalType={modalType}
            toggleModal={toggleModal}
            onAddToCart={this.onAddToCart}
            selectedSpecList={selectedSpecList}
            updateSelectSpecList={updateSelectSpecList} />
        }
      </div>
    )
  }
}

export default withStyles(styles)(ProductListContent)



